.title {
  font-family: 'Roboto', sans-serif;
  color: #111111;
  font-size: 48px;
  font-weight: 500;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.formStyle {
  width: 300px;
  background-color: white;
  padding: 20px;
  border: 1px solid black;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: baseline;
  margin-bottom: 20px;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 8px;
  /* margin-bottom: 20px; */
}

.span {
  font-family: 'Roboto', sans-serif;
  color: #111111;
  font-size: 24px;
  font-weight: 500;
}

.input {
  width: 150px;
}

.button {
  width: 120px;
  height: 24px;
  font-family: 'Roboto', sans-serif;
  color: #111111;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
}


